<template>
  <v-container>
    <h5 class="my-4">
      <span class="cblu bbred">City Deals {{city}}</span>
    </h5>
    <v-row>
      <v-col cols="12" md="3">
       <CatSidebar/>
      </v-col>
      <v-col cols="12" md="9">
        <div class="mobile">
          <v-card v-for="(cdeal,i) in cdeals" :key="i" class="item mitem my-2 mb-7">
            <div class="small d-flex">
              <router-link :to="{ name:'dealsdetail', params:{id:cdeal.id}}">
                <div class="mimg">
                  <v-img src="https://bizlx.itechvision.in/images/hot-deal-home-1.jpg" cover class="rounded-3" height="110"></v-img>
                </div>
              </router-link>
            <div class="mdetails">
              <router-link :to="{ name:'dealsdetail', params:{id:cdeal.id}}" class="mtitle text-decoration-none link-dark h4">
                      {{cdeal.hot_deal_title}}
              </router-link>
              <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
              <div class="cred">Price: ₹{{ cdeal.price_from }} - ₹{{ cdeal.price_to }}</div>
              <div class="fs11">Validity: {{ cdeal.date_from }} - {{ cdeal.date_to }}</div>
              <div class="address">
                <v-icon small>mdi-map-marker</v-icon>
                    {{ cdeal.city }}, {{ cdeal.state }}
              </div>
              <router-link :to="{ name:'dealsdetail', params:{id:cdeal.id}}" class="cred">Read More</router-link>
            </div>
            <div class="mcontact">
              <a :href="('https://wa.me/'+cdeal.mobile_phone+'?text=rf')">
                <v-btn fab small color="success" class="mb-5 btnsm"><v-icon>mdi-whatsapp</v-icon></v-btn>
              </a>
              <a :href="('tel:'+ cdeal.mobile_phone)">
                <v-btn fab small color="primary" class="btnsm"><v-icon>mdi-phone</v-icon></v-btn>
              </a>
            </div>
            </div>  
          </v-card>
        </div>
        <div class="desktop">
          <v-card v-for="(cdeal,i) in cdeals" :key="i" class="mb-7">
           <v-row>
            <v-col md="4" class="py-0">
              <router-link :to="{ name:'dealsdetail', params:{id:cdeal.id}}">
                 <v-img src="https://bizlx.itechvision.in/images/hot-deal-home-1.jpg" cover height="200"></v-img>
              </router-link>
            </v-col>
            <v-col cols="12" md="8" class="py-0">
              <v-card-text>
                <v-row>
                  <v-col cols="12" md="8">
                    <router-link :to="{ name:'dealsdetail', params:{id:cdeal.id}}" class="text-decoration-none link-dark h4">
                      {{cdeal.hot_deal_title}}
                    </router-link>
                    <div>Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                    <div class="cred">Price: ₹{{ cdeal.price_from }} - ₹{{ cdeal.price_to }}</div>
                    <div>Validity: {{ cdeal.date_from }} - {{ cdeal.date_to }} </div>
                    <div class="address">
                      <v-icon small>mdi-map-marker</v-icon>
                      {{ cdeal.address }} {{ cdeal.city }}, {{ cdeal.state }}
                      <router-link :to="{ name:'dealsdetail', params:{id:cdeal.id}}" class="cred">Read More</router-link>
                    </div>
                  </v-col>
                  <v-col md="4">
                    <a :href="('tel:'+ cdeal.mobile_phone)">
                    <v-btn small color="primary" class="me-1 mb-2">
                      <v-icon small>mdi-phone</v-icon>Call Now</v-btn>
                    </a>
                    <a :href="('https://wa.me/'+cdeal.mobile_phone+'?text=rf')">
                    <v-btn small color="success" class="mb-2">
                      <v-icon small>mdi-whatsapp</v-icon>WhatsApp</v-btn>
                    </a>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
          </v-row>
          </v-card>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import CatSidebar from "@/views/CatSidebar";
import axios from "axios";
export default {
  name: "CityDeals",
  components: {CatSidebar},
  metaInfo: {title: 'City Deals'},
  props:{
    city:String,
    id:Number,
  },
  data() {
    return {
      cdeals: [],
    };
  },
  methods:{
    getCitydealslist(){
      axios.get(`citydealslist/${this.city}`)
          .then((resp)=>{
            this.cdeals = resp.data.dealslist;
          });
    }
  },
  created() {
    this.getCitydealslist();
  }
}
</script>

<style scoped>
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 7px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 11px;}

</style>